import Vue from 'vue'
import Router from 'vue-router'

/**/
import come from '@/components/come/come'                                               /**/
import comePay from '@/components/come/comePay'                                         /**/
import promote from '@/components/come/promote'                                         /**/
import allMerchant from '@/components/come/allMerchant'                                 /**/
import tombola from '@/components/come/tombola'                                         /**/
import merchant from '@/components/come/merchant'                                       /**/

/**/
import carServeice from '@/components/carServeice/carServeice'                          /**/
import carWash from '@/components/carServeice/carWash'					                        /**/
import advise from '@/components/carServeice/advise'                                    /**/
import orderConfirm from '@/components/carServeice/orderConfirm'                        /**/
import maintain from '@/components/carServeice/maintain'                                /**/
import orderSuccess from '@/components/carServeice/orderSuccess'                        /**/
import carPaySuccess from '@/components/carServeice/carPaySuccess'                      /**/
/**/
import activity from '@/components/activity/activity'
/**/
import personCenter from '@/components/personCenter/personCenter'                       /**/
import personOrder from '@/components/personCenter/personOrder'                         /**/
import personOrderCancel from '@/components/personCenter/personOrderCancel'             /**/
import personOrderComplete from '@/components/personCenter/personOrderComplete'         /**/
import personOrderIn from '@/components/personCenter/personOrderIn'                     /**/

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'come',
      component: come,
      meta: { navShow: true,name:''}
    },
    {
      path: '/come',
      name: 'come',
      component: come,
      meta: { navShow: true,name:''}
    },
    {
      path: '/comePay',
      name: 'comePay',
      component: comePay,
      meta: { navShow: false,name:''}
    },
    {
      path: '/carServeice',
      name: 'carServeice',
      component: carServeice,
      meta: { navShow: true,name:''}
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity,
      meta: { navShow: true,name:''}
    },
    {
      path: '/personCenter',
      name: 'personCenter',
      component: personCenter,
      meta: { navShow: true,name:''}
    },
    {
      path: '/personOrder',
      name: 'personOrder',
      component: personOrder,
      meta: { navShow: false,name:''}
    },
    {
      path: '/personOrderCancel',
      name: 'personOrderCancel',
      component: personOrderCancel,
      meta: { navShow: false,name:''}
    },
    {
      path: '/personOrderComplete',
      name: 'personOrderComplete',
      component: personOrderComplete,
      meta: { navShow: false,name:''}
    },
    {
      path: '/personOrderIn',
      name: 'personOrderIn',
      component: personOrderIn,
      meta: { navShow: false,name:''}
    },
    {
      path: '/promote',
      name: 'promote',
      component: promote,
      meta: { navShow: false,name:''}
    },
    {
      path: '/allMerchant',
      name: 'allMerchant',
      component: allMerchant,
      meta: { navShow: false,name:''}
    },
    {
      path: '/tombola',
      name: 'tombola',
      component: tombola,
      meta: { navShow: false,name:''}
    },
    {
      path: '/merchant',
      name: 'merchant',
      component: merchant,
      meta: { navShow: false,name:''}
    },
    {
      path: '/carWash',
      name: 'carWash',
      component: carWash,
      meta: { navShow: false,name:''}
    },
    {
      path: '/advise',
      name: 'advise',
      component: advise,
      meta: { navShow: false,name:''}
    },
    {
      path: '/orderConfirm',
      name: 'orderConfirm',
      component: orderConfirm,
      meta: { navShow: false,name:''}
    },
    {
      path: '/maintain',
      name: 'maintain',
      component: maintain,
      meta: { navShow: false,name:''}
    },
    {
      path: '/orderSuccess',
      name: 'orderSuccess',
      component: orderSuccess,
      meta: { navShow: false,name:''}
    },
    {
      path: '/carPaySuccess',
      name: 'carPaySuccess',
      component: carPaySuccess,
      meta: { navShow: false,name:''}
    }
    
  ]
})



